  import {footPrintList, memberInfo} from '@/api/userInfo';
	import {cartItemCount} from "@/api/product";
	import listCell from '@/components/rf-list-cell';
	import {mapMutations} from 'vuex';
	import rfBadge from '@/components/rf-badge/rf-badge'
	let startY = 0, moveY = 0, pageAtTop = true;
  export default {
		components: {
			listCell,
			rfBadge
		},
		data(){
			return {
				settingList: [
					{icon: 'iconiconfontweixin', url: '/pages/user/coupon/list', title: '去领券中心', color: '#e07472'},
					{icon: 'icondizhi', url: '', title: '地址管理', color: '#5fcda2'},
					{icon: 'iconshoucang_xuanzhongzhuangtai', url: '/pages/user/address/address', title: '我的收藏', color: '#5fcda2'},
					{icon: 'iconshare', url: '', title: '分享', color: '#9789f7'},
					{icon: 'iconshezhi1', url: '/pages/set/set', title: '设置', color: '#e07472'},
				],
				coverTransform: 'translateY(0px)',
				coverTransition: '0s',
				moving: false,
				userInfo: {
					promoter: null,
				},
				token: null,
				footList: [],
				user_info: {},
				orderSectionList: [
					{ title: '待付款', icon: 'iconfont icondaifukuan', url: '/pages/order/order?state=0' },
					{ title: '待发货', icon: 'iconfont iconshouye', url: '/pages/order/order?state=1' },
					{ title: '待收货', icon: 'iconfont iconyishouhuo', url: '/pages/order/order?state=2' },
					{ title: '评价', icon: 'iconfont iconpingjia', url: '/pages/order/order?state=3' },
					{ title: '退款/售后', icon: 'iconfont iconshouhoutuikuan', url: '/pages/order/refund' },
				],
				amountList: [
					{ title: '余额', value: 0, url: '/pages/user/account/account' },
					{ title: '优惠券', value: 0, url: '/pages/user/coupon/coupon?type=1' },
					{ title: '积分', value: 0, url: '/pages/user/account/integral' }
				],
				loading: true,
			}
		},
		onShareAppMessage() {
      return {
        title: '欢迎来到RageFrame商城',
        path: `/pages/index/index`
      }
		},
    async onShow() {
	    this.initData();
    },
		async onLoad(){
		},




















		methods: {
			shareToH5() {



			},
			...mapMutations(['login']),
			/**
			 *@des 分享
			 *@author stav stavyan@qq.com
			 *@blog https://stavtop.club
			 *@date 2019/11/25 15:53:35
			 */
			share(){
				this.$refs.share.toggleMask();
			},
			// 数据初始化
			async initData() {
				this.userInfo = uni.getStorageSync('userInfo') || {};
				this.token = uni.getStorageSync('accessToken') || undefined;
				if (this.token) {
					await this.getMemberInfo();
		      if (uni.getStorageSync('cartNum')) {
		      	if (uni.getStorageSync('cartNum') != 0) {
					    uni.setTabBarBadge({
						    index: 2,
						    text: uni.getStorageSync('cartNum').toString(),
					    });
			      } else {
			        uni.removeTabBarBadge({index: 2});
			      }
			    } else {
		        uni.removeTabBarBadge({index: 2});
		      }
				} else {
			    this.loading = false;
			    uni.removeTabBarBadge({index: 2});
          this.amountList[0].value = 0;
          this.amountList[1].value = 0;
          this.amountList[2].value = 0;
				}
			},
			// 获取用户信息
      async getMemberInfo() {
		   this.loading = true;
			 this.$get(memberInfo).then(r => {
				  this.loading = false;
          this.userInfo = r.data || undefined;
				  const orderSynthesizeNumArr = [];
					for (let item in r.data.order_synthesize_num) {
						orderSynthesizeNumArr.push(r.data.order_synthesize_num[item])
					}
					for (let i = 0; i < this.orderSectionList.length; i++) {
						this.orderSectionList[i].num = orderSynthesizeNumArr[i].toString();
					}
          this.amountList[0].value = r.data.account.user_money || 0;
          this.amountList[1].value = r.data.coupon_num || 0;
          this.amountList[2].value = r.data.account.user_integral || 0;
					this.getFootPrintList();
	        this.$get(`${cartItemCount}`).then(r => {
				      if (r.data == 0) {
				        uni.removeTabBarBadge({index: 2});
					    } else {
						    uni.setStorageSync('cartNum', r.data);
						    uni.setTabBarBadge({
							    index: 2,
							    text: r.data,
						    });
				      }
				    });
          uni.setStorage({
              key: 'userInfo',
              data: r.data
          })
        }).catch(() => {
				  this.loading = false;
			 });
      },
			// 获取足迹列表
			async getFootPrintList() {
				await this.$get(`${footPrintList}`).then(r => {
					this.footList = r.data
				});
			},
			// 统一跳转接口,拦截未登录路由
			navTo(url){
				if (!url) {
					return;
				}
				if(!this.token){
					url = '/pages/public/logintype';
					uni.showModal({
            content: '你暂未登陆，是否跳转登录页面？',
            success: (confirmRes)=> {
              if (confirmRes.confirm) {
			          uni.navigateTo({
			            url
			          });
              }
            }
          });
				} else {
          uni.navigateTo({
            url
          })
        }
			},
			directTo (url){
        uni.navigateTo({
          url
        })
			},
			/**
			 *  会员卡下拉和回弹
			 *  1.关闭bounce避免ios端下拉冲突
			 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
			 *    transition设置0.1秒延迟，让css来过渡这段空窗期
			 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
			 */
			coverTouchstart(e){
				if(pageAtTop === false){
					return;
				}
				this.coverTransition = 'transform .1s linear';
				startY = e.touches[0].clientY;
			},
			coverTouchmove(e){
				moveY = e.touches[0].clientY;
				let moveDistance = moveY - startY;
				if(moveDistance < 0){
					this.moving = false;
					return;
				}
				this.moving = true;
				if(moveDistance >= 80 && moveDistance < 100){
					moveDistance = 80;
				}
				if(moveDistance > 0 && moveDistance <= 80){
					this.coverTransform = `translateY(${moveDistance}px)`;
				}
			},
			coverTouchend(){
				if(this.moving === false){
					return;
				}
				this.moving = false;
				this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
				this.coverTransform = 'translateY(0px)';
				}
			}
	}
